

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html,body{
  font-size:62.5%;
  font-family: 'Raleway', sans-serif;
}


.header{
  text-align: center;
  text-transform: uppercase;
  font-size:1.6rem;
  letter-spacing: 0.2rem;
  border-bottom: 1px solid rgba(156, 156, 156, 0.5);
  
}
.header__primary{
  padding:3.5rem 0;
  color:#2d2b2b;
  position: relative;
  
}
.header__primary h1{
  font-weight: 400;
  display: inline-block;
}

/* Hamburger Menu */
.header__primary-hamburger {
  display: none;
  position: absolute;
  top: 42%;
  right: 5%;
  padding: 2rem;
  cursor: pointer;
}

.header__primary-hamburger span:nth-child(1){
  width: 4rem;
  content: '';
  position: absolute;
  top: 0.5rem;
  height: 0.2rem;
  background: black;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.header__primary-hamburger span:nth-child(2){
  width: 4rem;
  content: '';
  position: absolute;
  top: 1.5rem;
  height: 0.2rem;
  background: black;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.header__primary-hamburger span:nth-child(3){
  width: 4rem;
  content: '';
  position: absolute;
  top: 2.5rem;
  height: 0.2rem;
  background: black;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}


.header__secondary {
  max-width: 156rem;
  margin: 0 auto;
  padding-bottom: 2rem;
}
.header__secondary ul {
  display: flex;
  justify-content: space-evenly;
  list-style: none;
}
.header__secondary ul img {
  width:2rem;
  height: 2rem;
}
.header__secondary :link,
.header__secondary :visited {
  text-decoration: none;
  color:#2d2b2b;
  font-weight: 600;
}


@media (min-width:320px) and (max-width:780px){
html {
  font-size:6px;
}
.header__primary-hamburger {
  display: inline-block;
}
.header__secondary{
  display: none;
  position: absolute;
  right: 0;
  padding: 0 5%;
  border: 1px solid rgba(156, 156, 156, 0.5);
}
.header__secondary ul {
  display: block;
}
.header__secondary img {
  padding: 1.5rem;
}
.header__secondary :link,
.header__secondary :visited {
  display: block;
  padding: 1.5rem;
}
}
